import Swiper, { Navigation, Pagination, Autoplay } from "swiper";
Swiper.use([, Navigation, Pagination, Autoplay]);
export default {
  props: {
    projects: {
      type: Array,
      default: [],
    },
  },
  data: () => ({
    defaultImage: require("@/assets/images/qwe.png"),
    projectOptions: {
      slidesPerView: 1,
      spaceBetween: 20,
      speed: 500,
      transition: 5000,
      loop: true,
      direction: "horizontal",
      navigation: {
        nextEl: ".swiper1-next",
        prevEl: ".swiper1-prev",
      },
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      autoplay: {
        delay: 5000,
      },
      breakpoints: {
        1264: {
          slidesPerView: 4,
        },
        960: {
          slidesPerView: 3,
        },
        600: {
          slidesPerView: 2,
        },
      },
    },
  }),
  methods: {
    goToInner(id) {
      this.$router.push(
        this.localePath({
          path: `/Projects/${id}`,
          query: {
            id: id,
          },
        })
      );
    },
  },
};
